//第二种方式
//import Vue from 'vue';
//export default new Vue({});

//第三种方式，vue模块使用无需导出,全局注册，js模块使用就需要导出
import Vue from 'vue';
const app = new Vue({});
/*
 * 事件名：mainScroll
 * 含义：主区域滚动条位置变化后触发
 * 参数：
 * - 滚动的dom元素，如果是undefined，则表示dom元素已经不存在
 *
 * 事件名：setMainScroll
 * 含义：当需要设置主区域滚动条位置时触发
 * 参数：
 * - 滚动高度
 */
//全局注册
Vue.prototype.$bus = app;

//导出给js模块使用
export default app;
