<template>
  <i class="iconfont icon-container" :class="fontClass"></i>
</template>

<script>
const classMap = {
  home: 'iconzhuye',
  success: 'iconzhengque',
  error: 'iconcuowu',
  close: 'iconguanbi',
  warn: 'iconjinggao',
  info: 'iconxinxi',
  blog: 'iconblog',
  code: 'iconcode',
  about: 'iconset_about_hov',
  weixin: 'iconweixin',
  mail: 'iconemail',
  github: 'icongithub',
  qq: 'iconsign_qq',
  arrowUp: 'iconiconfonticonfonti2copy',
  arrowDown: 'iconiconfonticonfonti2',
  empty: 'iconempty',
  chat: 'iconliuyan',
};
export const types = Object.keys(classMap); //返回一个由一个给定对象的自身可枚举属性组成的数组
export default {
  props: {
    type: {
      type: String,
      required: true,
    },
  },

  computed: {
    //图标样式
    fontClass() {
      return classMap[this.type];
    },
  },
};
</script>

<style scoped>
@import '//at.alicdn.com/t/font_2164449_nalfgtq7il.css';
.iconfont {
  color: inherit;
  font-size: inherit;
}
</style>
