//思路：在钩子函数参数当中拿到一些我们想要的参数，再定义一个数组，
//把参数放到数组当中，我们就可以玩数据了，一开始也会进行图片处理
//不符合要求的图片，则会在再触发滚动条事件时候再次运行

import eventBus from '@/eventBus';
import { debounce } from '@/utils';
import defaultGif from '@/assets/default.gif';

let imgs = [];

function setImage(img) {
  img.dom.src = defaultGif; // 先暂时使用着默认图片
  // 处理图片
  // 该图片是否在视口范围内
  const clientHeight = document.documentElement.clientHeight;
  const rect = img.dom.getBoundingClientRect();
  const height = rect.height || 150; //图片可能没有加载出来就没有高度，就使用默认值
  if (rect.top >= -height && rect.top <= clientHeight) {
    //在元素的可见上方和在元素的可见下方(在视口范围)
    img.dom.src = img.src;
    imgs = imgs.filter((i) => i !== img); //在数组里面删除已经处理过的图片，让滚动滚动条的时候不用重复处理
  }
}

//调用该函数，就可以设置那些合适的图片
function setImages() {
  for (const img of imgs) {
    // 处理该图片
    setImage(img);
  }
}

function handleScroll() {
  setImages();
}

eventBus.$on('mainScroll', debounce(handleScroll, 50));

export default {
  inserted(el, bindings) {
    //被绑定元素插入父节点时调用
    const img = {
      dom: el,
      src: bindings.value,
    };
    imgs.push(img);
    //立即处理它
    setImage(img);
  },
  unbind(el) {
    //el是旧的
    //只调用一次，指令与元素解绑时调用
    imgs = imgs.filter((img) => img.dom !== el);
    //切换数据的时候，把原来的图片删掉，只要新绑定的图片
  },
};
