import axios from 'axios';
import { showMessage } from '@/utils';

const ins = axios.create(); //创建一个axios的实例
ins.interceptors.response.use(function (resp) {
  //创建一个响应拦截器
  if (resp.data.code !== 0) {
    showMessage({
      content: resp.data.msg,
      type: 'error',
      duration: 2000,
    });
    return null;
  }
  return resp.data.data;
});

export default ins;
