import { titleController } from '@/utils';
import Vue from 'vue';
import VueRouter from 'vue-router';
import routes from './routes';
Vue.use(VueRouter); //使用一个vue插件
//并且全局注册了<RouterView />这个组件，用于占位
//提供了全局的组件`RouterLink`，它的渲染结果是一个`a`元素
//并给vue实例注入了一些对象，其中$route对象是路由的各种信息
//$router对象是用来在代码当中无刷新跳转

const router = new VueRouter({
  //配置
  routes, //路由匹配规则
  mode: 'history',
});
router.afterEach((to, from) => {
  //导航守卫，路由跳转后执行
  if (to.meta.title) {
    titleController.setRouteTitle(to.meta.title); //设置路由标题
  }
});

export default router;
