//（动态导入）异步组件通常应用在路由懒加载中，以达到更好的分包
import 'nprogress/nprogress.css'; //导入进度条
import { start, done, configure } from 'nprogress'; //具名导入，这样打包结果会小很多

configure({
  trickleSpeed: 20, //进度条速度设置
  showSpinner: false, //是否展现小转圈
});

function delay(duration) {
  return new Promise((resolve) => {
    setTimeout(() => {
      resolve();
    }, duration);
  });
}

function getPageComponent(pageCompResolver) {
  //pageCompResolver传入一个函数返回一个组件对象
  return async () => {
    start(); //进度条开始
    if (process.env.NODE_ENV === 'development') {
      //如果是开发环境，先等待两秒看效果
      await delay(2000);
    }
    const comp = await pageCompResolver(); //调用函数，返回一个promise，其实就是一个组件
    done(); //进度条结束
    return comp;
  };
}

export default [
  {
    name: 'Home', //具名路由匹配规则
    path: '/',
    component: getPageComponent(
      () => import(/* webpackChunkName: "home" */ '@/views/Home') //webpackChunkName分包的名字
    ), //import('@/views/Home')返回一个promise，其实就是一个组件
    meta: {
      //meta自定义的一些路由属性可以写在这
      title: '首页',
    },
  },
  {
    name: 'About',
    path: '/about',
    component: getPageComponent(() =>
      import(/* webpackChunkName: "about" */ '@/views/About')
    ),
    meta: {
      title: '关于我',
    },
  },
  {
    name: 'Blog',
    path: '/article',
    component: getPageComponent(() =>
      import(/* webpackChunkName: "blog" */ '@/views/Blog')
    ),
    meta: {
      title: '文章',
    },
  },
  {
    name: 'CategoryBlog',
    path: '/article/cate/:categoryId',
    component: getPageComponent(() =>
      import(/* webpackChunkName: "blog" */ '@/views/Blog')
    ),
    meta: {
      title: '文章',
    },
  },
  {
    name: 'BlogDetail',
    path: '/article/:id',
    component: getPageComponent(() =>
      import(/* webpackChunkName: "blogdetail" */ '@/views/Blog/Detail')
    ),
    meta: {
      title: '文章详情',
    },
  },
  {
    name: 'Project',
    path: '/project',
    component: getPageComponent(() =>
      import(/* webpackChunkName: "project" */ '@/views/Project')
    ),
    meta: {
      title: '项目&效果',
    },
  },
  {
    name: 'Message',
    path: '/message',
    component: getPageComponent(() =>
      import(/* webpackChunkName: "message" */ '@/views/Message')
    ),
    meta: {
      title: '留言板',
    },
  },
];

//路由后面加参数不会影响路由的匹配，例如加?page=2,limit=3,只是让路由的信息更多
//路由的路径（path）多了，或者少了，会影响路由的匹配，例如原本是/article 后面变成/article/123
